.sidebar {
  position: absolute;
  top: 0;
  left: 0;
  width: 370px;
  height: 100vh;
  max-width: 100%;
  background-color: #fff;
  z-index: 10;
}

.sidebar.open {
}

.background {
  position: absolute;
  background-color: rgba(0, 0, 0, 1);
  width: 100vw;
  height: 100vh;
  opacity: 0.3;
  z-index: 5;
}

.nav {
  position: absolute;
  top: 64px;
  left: 70px;
  display: flex;
  flex-direction: column;
  background-color: #fff;
}

.nav a {
  margin-bottom: 45px;
  font-weight: 600;
  font-size: 22px;
  line-height: 26px;
  text-decoration: none;
  color: #000;
}

.nav a:hover {
  color: #58acff;
}

.nav a.active {
  color: #0059b2;
  text-decoration: underline;
  text-decoration-color: #0059b2;
}

.nav a:active {
  color: #3d6b99;
}
.close {
  top: 16px;
  right: 24px;
  position: absolute;
  cursor: pointer;
  border: none;
  background-color: transparent;
}

.close img {
}
