.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 70px 16px;
  height: 60px;
}

.header::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 1px;
  top: 60px;
  left: 0;
  background-color: #1d21261a;
  box-shadow: 0 0 2px 0 rgba(29, 33, 38, 0.1);
  z-index: 2;
}

.burgerMenuIcon {
  cursor: pointer;
}

.header h1 {
  font-weight: 600;
  font-size: 24px;
  line-height: 30;
  color: #000;
}
